<template>
    <div style="background: #fff">
        <div style="height: 0.88rem;"></div>
        <my-header-list>确认订单</my-header-list>
        <div class="address-box">
            <div class="shouhuo">
                收货地址:
                <img src="../../style/img/back.png" alt="">
            </div>
            <div class="content-box" v-if="address">
                <div class="zhuzhi">住址</div>
                <div class="didian">{{address.address}}</div>
                <div class="name-box">
                    <span class="name">{{address.names}}</span>
                    <span class="tel">{{address.tell}}</span>
                </div>
            </div>
        </div>
        <div class="order-box" v-if="orders">
            <div class="shouhuo">确认订单:</div>
            <div class="goods-box" v-for="item in orders.goods" :key="item.gid">
                <img :src="imgHostName + item.gthumb" :alt="item.gname">
                <div class="detail">
                    {{item.gname}}
                </div>
                <div class="sale">￥{{item.sale}}</div>
                <div class="num">×{{item.num}}</div>
            </div>
            <div class="price-box" v-if="orders">
                <span>商品总价</span>
                <span style="color: #ff9201;">￥{{orders.price}}</span>
            </div>
            <div class="beizhu-box">
                <van-cell-group>
                    <van-field v-model="orders.remarks" rows="2" autosize label="备注" type="textarea" maxlength="50" placeholder="请输入备注" show-word-limit/>
                </van-cell-group>
            </div>
        </div>
        <div class="dingdan-box" v-if="orders">
            <div class="shouhuo">订单信息:{{orders.status==0?'代付款':''}}</div>
            <div class="price-box">
                <span>订单编号</span>
                <span>{{orders.ordernum}}</span>
            </div>
            <div class="price-box">
                <span>下单时间</span>
                <span>{{orders.ctime}}</span>
            </div>
        </div>
        <div style="height: 0.9rem"></div>
        <div class="pay-box">
            <div class="fukuan">
                <span>付款：</span>
                <span style="color: #ff9201;">￥{{orders.price}}</span>
            </div>
            <div class="pay" @click="goPay">去支付</div>
        </div>
    </div>
</template>

<script>
    import MyHeaderList from '../../components/my-header/my-header-list';
    import {queryOrders,payOrders} from "../../api/orders";
    import {queryAddress} from "../../api/address";
    import {IMGHOSTNAME,SUCCESS} from "../../config/base";

    export default {
        name: "orders",
        components:{
            MyHeaderList,
        },
        data(){
            return {
                address:null,
                orders:null,
                imgHostName:IMGHOSTNAME,
                ordernum:0,
            }
        },
        methods:{
            //获取地址信息
            setAddress(){
                queryAddress('/api/address/1').then(res=>{
                    if(res.code == SUCCESS && res.address){
                        this.address = res.address;
                    }
                })
            },
            // 获取订单信息
            setOrders(){
                queryOrders('/api/orders/'+this.ordernum).then(res=>{
                    if(res.code == SUCCESS){
                        this.orders = res.data;
                    }
                })
            },
            goPay(){
                payOrders('api/orders/'+this.ordernum,{aid:this.address.aid,remarks:this.orders.remarks})
                    .then(res=>{
                        window.console.log(res)
                    })
            }
        },
        beforeMount() {
            this.ordernum = this.$route.query.ordernum;
            this.setAddress();
            this.setOrders();
        }
    }
</script>

<style scoped>

    .address-box{
        width: 7.04rem;
        height: 2.74rem;
        border: #EDEDED 1px solid;
        border-radius: 0.1rem;
        margin: 0.4rem auto 0 ;
    }
    .shouhuo{
        width: 6.4rem;
        height: 0.48rem;
        font-size: 0.28rem;
        margin: 0 auto;
        border-bottom: #FAFAFA 1px solid;
        margin-top: 0.28rem;
    }
    .shouhuo img{
        width: 0.3rem;
        margin-left: 4.8rem;
    }
    .content-box{
        width: 6.4rem;
        height: 1.5rem;
        margin: 0.2rem auto 0;
        padding: 0.2rem ;
        box-sizing: border-box;
    }
    .zhuzhi{
        width: 0.56rem;
        height: 0.34rem;
        border-radius: 0.06rem;
        background-color: #ff9201;
        font-size: 0.2rem;
        line-height: 0.34rem;
        text-align: center;
        color: #fff;
    }
    .didian{
        font-size: 0.24rem;
        line-height: 0.5rem;
    }
    .name-box{
        font-size: 0.3rem;
        line-height: 0.5rem;
    }
    .name-box .tel{
        margin-left: 0.2rem;
    }
    .order-box{
        width: 7.04rem;
        border: #EDEDED 1px solid;
        border-radius: 0.1rem;
        margin: 0.4rem auto 0 ;
    }
    .goods-box{
        width:100%;
        height: 0.9rem;
        padding: 0 0.2rem;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        margin-top: 0.2rem;
    }
    .goods-box img{
        width: 0.9rem;
        height: 0.9rem;
    }
    .goods-box .detail{
        width: 2.6rem;
        height: 0.7rem;
        margin-top: 0.1rem;
        font-size: 0.24rem;
        overflow: hidden;
        text-overflow: ellipsis;
        display:-webkit-box;
        -webkit-box-orient:vertical;
        -webkit-line-clamp:2;
    }
    .goods-box .num{
        margin-top: 0.25rem;
        font-size: 0.3rem;
        color: #777;
    }
    .goods-box .sale{
        color: #ff9201;
        margin-top: 0.25rem;
    }
    .price-box{
        width: 6.4rem;
        height: 0.6rem;
        padding: 0.1rem 0;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        font-size: 0.26rem;
        color: #333;
        margin: 0.2rem auto 0;
        border-bottom: #EDEDED 1px solid;
    }
    .beizhu-box{
        width: 6.4rem;
        padding: 0.1rem 0;
        margin: 0.2rem auto 0;
    }
    .dingdan-box {
        width: 7.04rem;
        height: 2.4rem;
        border: #EDEDED 1px solid;
        border-radius: 0.1rem;
        margin: 0.4rem auto 0.2rem;
    }
    .pay-box{
        position: fixed;
        left: 0;
        bottom:0;
        width: 100%;
        height: 0.9rem;
        background: #fff;
        border-top: #EDEDED 1px solid;
        display: flex;
        justify-content: space-between;
    }
    .pay-box .fukuan{
        padding: 0.2rem 0 0 0.2rem;
    }
    .pay-box .pay{
        width: 2.02rem;
        height: 100%;
        background: #ff9201;
        color: #fff;
        text-align: center;
        line-height: 0.9rem;
    }
</style>
